import {ContentChildren, Input, QueryList, SimpleChanges, TemplateRef} from '@angular/core';
import {CommonsUtils} from '@er/utils';
import {SelectorDirective} from '../directives/selector/directive';
import {TypedTemplateDirective} from '../directives/typed-template/directive';
import {BaseDirective} from './directive';

/**
 * provide template & dynamic query support
 */
export abstract class BaseComponent extends BaseDirective {

  @Input() customerTpl: TemplateRef<any>;

  @ContentChildren(TypedTemplateDirective) typedTemplates: QueryList<TypedTemplateDirective>;

  selectors: QueryList<SelectorDirective>;

  templateNames: {};

  templates: { [type: string]: TemplateRef<any> | {} } = {};

  ngAfterContentInit(): void {
    if (this.typedTemplates && this.typedTemplates.length > 0) {
      this.typedTemplates.forEach((item) => {
        if (item.type) {
          CommonsUtils.set(this.templates, item.type, item.template);
        } else {
          this.templates['_'] = item.template;
        }
      });
    }
  }

  eventHandler(outputs, eventName, defaultHandler, event) {
    if (outputs && outputs[eventName]) {
      return outputs[eventName].call(this, event);
    } else {
      return defaultHandler(event);
    }
  }

  template(name?: string, ...defaultTpls: TemplateRef<any>[]): TemplateRef<any> | {} {
    if (!name) {
      name = '_';
    }
    if (this.templates[name]) {
      return this.templates[name];
    } else if (defaultTpls.length > 0) {
      const tpls = CommonsUtils.filter(defaultTpls, defaultTpl => !!defaultTpl);
      if (tpls.length > 0) {
        return tpls[0];
      }
    }
    return undefined;
  }

  getComponentBySelectorId<C>(id: any): C {
    if (this.selectors) {
      const matched: SelectorDirective[] = this.selectors.toArray().filter(comp => comp.id === id);
      if (matched.length === 1) {
        return matched[0].hostComponent;
      }
    }
    return undefined;
  }

  hasTemplate(name: string, ...defaultTpls: TemplateRef<any>[]): boolean {
    return this.template(name, ...defaultTpls) !== undefined;
  }

  hasChildren(templates) {
    return CommonsUtils.isJson(templates);
  }


  // empty hooks for dyna bind

  ngAfterContentChecked() {
  }

  ngAfterViewInit() {
    this['__ngAfterViewInit'] = true;
  }

  ngAfterViewChecked() {
  }

  ngDoCheck() {
  }

  ngOnInit() {
    this['__ngOnInit'] = true;
  }

  ngOnChanges(changes: SimpleChanges) {
  }

  ngOnDestroy() {
  }

}
