import {Injectable} from '@angular/core';
import {ActivatedRouteSnapshot, Resolve, RouterStateSnapshot} from '@angular/router';
import {Observable} from 'rxjs';

/**
 * resolve page model from router
 */
@Injectable({
  providedIn: 'root'
})
export class ResourceResolver implements Resolve<any> {


  resolve(route: ActivatedRouteSnapshot, state: RouterStateSnapshot): Observable<{ [resourceKey: string]: any }> {
    if (!route.data || !route.data['props']) { return; }
  }

}
