import {ChangeDetectionStrategy, ChangeDetectorRef, Component, EventEmitter, Input, OnInit, Output} from '@angular/core';
import {StateNames} from '@er/types';
import {StatesUtils} from '@er/utils';
import {Subject} from 'rxjs';
import {filter, map} from 'rxjs/operators';
import {BaseComponent} from '../../bases/component';

@Component({
  selector: 'er-button',
  templateUrl: 'tpl.html',
  changeDetection: ChangeDetectionStrategy.OnPush
})
export class StateButtonComponent extends BaseComponent implements OnInit {

  @Input() type: 'button' | 'link' = 'button';
  @Input() hint = '';
  @Input() label: string;
  @Input() icon: string;
  @Input() style: {};
  @Input() hidden: boolean;
  @Input() styleClass: string;
  @Input() disabled: boolean;
  @Input() iconRight: boolean;
  @Input() stateKey: string;
  @Input() stateLabel: string;
  @Input() stateHint: string;
  @Input() context: {};

  @Output() onClick: EventEmitter<any> = new EventEmitter();
  @Output() onFocus: EventEmitter<any> = new EventEmitter();
  @Output() onBlur: EventEmitter<any> = new EventEmitter();

  processIcon = 'fa fa-cog fa-spin';

  processing$: Subject<boolean> = new Subject<boolean>();

  constructor(protected cd: ChangeDetectorRef) {
    super();
  }

  @Input()
  set processing(processing: boolean) {
    this.processing$.next(processing);
    this.cd.markForCheck(); // used for not dom event modify
  }

  get btnLabel() {
    if (this.processing) {
      return this.stateLabel || this.label;
    } else {
      return this.label || '';
    }
  }

  get btnTitle() {
    if (this.processing) {
      return this.stateHint || 'æ§è¡ä¸­...';
    } else {
      return this.hint || '';
    }
  }

  ngOnInit() {
    if (this.stateKey) {
      this.$subscriptions = StatesUtils.observe(StateNames.http, this.stateKey)
        .pipe(
          filter(state => !!state),
          map(state => state['start'])
        ).subscribe(b => this.processing$.next(b));
    }
  }

  click(event) {
    this.onClick.emit({
      ...event,
      ...this.context || {}
    });
  }
}
