import {ChangeDetectionStrategy, Component, Input} from '@angular/core';
import {BaseComponent} from '../../../bases/component';

export enum MobileType {
  iphoneX = 'iphone-x',
  iphone8 = 'iphone8',
  iphone8plus = 'iphone8plus',
  iphone5s = 'iphone5s',
  nexus5 = 'nexus5',
  ipad = 'ipad',
  note8 = 'note8',
  macbook = 'macbook'
}

@Component({
  selector: 'er-phone-preview',
  templateUrl: 'tpl.html',
  styleUrls: ['styles.scss'],
  changeDetection: ChangeDetectionStrategy.OnPush
})
export class PhonePreviewComponent extends BaseComponent {
  MOBILE_TYPE = MobileType;
  @Input() mobile: MobileType = MobileType.iphone8;
  @Input() color: 'black' | 'gold' | 'silver';
  @Input() isLandscape = false;
  @Input() scale = 1;

  zoomIn() {
    if (this.scale > 0.25) {
      this.scale = (this.scale - 0.25);
    }
  }

  zoomOut() {
    if (this.scale < 2) {
      this.scale = (this.scale + 0.25);
    }
  }
}

