import {Pipe, PipeTransform} from '@angular/core';
import {IdCardUtils} from '@er/utils';

@Pipe({name: 'idCard'})
export class IdCardPipe implements PipeTransform {
  transform(idCardNo: any, attr?: 'gender' | 'birthday' | 'age') {
    const attrs = IdCardUtils.parse(idCardNo);
    if (attrs && Object.keys(attrs).length > 0) {
      return attr ? attrs[attr] : attrs;
    } else {
      return '';
    }
  }
}
