import {HostBinding, Input} from '@angular/core';
import {ControlValueAccessor} from '@angular/forms';
import {NO_OP, Props} from '@er/types';
import {CommonsUtils} from '@er/utils';
import {WrapperComponent} from './wrappe';


export class ControlWrapperComponent<T extends Props = Props> extends WrapperComponent<T> implements ControlValueAccessor {

  @Input() valueParser: (value) => any;

  @Input() modelFormatter: (model) => any;

  @Input() resettable = true;

  @Input() disabled = false;

  onTouched: () => void = NO_OP;
  onChange: (_: any) => void = NO_OP;

  protected innerValue: any;

  @HostBinding('style.opacity')
  get opacity() {
    return this.disabled ? 0.25 : 1;
  }

  @HostBinding('style.cursor')
  get cursor() {
    return this.disabled ? 'not-allowed' : 'default';
  }

  get value(): any {
    return this.innerValue;
  }

  set value(value: any) {
    const v = this.valueParser ? this.valueParser(value) : value;
    if (!CommonsUtils.isEqual(v, this.innerValue)) {
      this.innerValue = v;
      this.onChange(v);
    }
  }

  updateTouch() {
    this.onTouched();
  }

  writeValue(model: any) {
    const v = this.modelFormatter ? this.modelFormatter(model) : model;
    if ( v !== this.innerValue) {
      this.innerValue = v;
    }
  }

  registerOnChange(fn: any) {
    this.onChange = fn;
  }

  registerOnTouched(fn: any) {
    this.onTouched = fn;
  }

  setDisabledState(isDisabled: boolean): void {
    this.disabled = isDisabled;
  }

  onPropsInit() {
    if (this.disabled) {
      this.$props['disabled'] = this.disabled;
    }
  }

  reset() {
    this.value = null;
  }
}
