import {Pipe, PipeTransform} from '@angular/core';
import {CommonsUtils} from '@er/utils';

@Pipe({
  name: 'erSub'
})
export class SubPipe implements PipeTransform {
  transform(value: any, start?: number, end?: number) {
    if (!value) { return; }
    const iStart = start || 0;
    let iEnd = end;
    if (CommonsUtils.isString(value)) {
      if (!iEnd) { iEnd = value.length; }
      return value.substr(iStart, iEnd) + '...';
    } else if (CommonsUtils.isArray(value)) {
      if (!iEnd) { iEnd = value.length; }
      return (<any[]>value).slice(iStart, iEnd);
    }
    return value;
  }
}
