import {ChangeDetectionStrategy, ChangeDetectorRef, Component, forwardRef, Input} from '@angular/core';
import {AbstractControl, NG_VALIDATORS, NG_VALUE_ACCESSOR, ValidationErrors, Validator} from '@angular/forms';
import {DataEntity, DataField, DataItem, QueryBuilderProps, QueryRule, QueryRuleSet, THEME_COLORS} from '@er/types';
import {CommonsUtils} from '@er/utils';
import {ControlComponent} from '../../bases/control';
import {LogUtils} from '../../utils/log';
import {DEFAULT_QUERY_BUILDER_PROPS, EMPTY_QUERY_RULE_SET} from './defaults';


const CONTROL_VALUE_ACCESSOR: any = {
  provide: NG_VALUE_ACCESSOR,
  useExisting: forwardRef(() => QueryBuilderComponent),
  multi: true
};

const VALIDATOR: any = {
  provide: NG_VALIDATORS,
  useExisting: forwardRef(() => QueryBuilderComponent),
  multi: true
};

@Component({
  selector: 'er-query-builder',
  templateUrl: 'tpl.html',
  styleUrls: ['styles.scss'],
  providers: [CONTROL_VALUE_ACCESSOR, VALIDATOR],
  changeDetection: ChangeDetectionStrategy.OnPush
})
export class QueryBuilderComponent extends ControlComponent implements Validator {

  @Input() ruleSet: QueryRuleSet = {condition: 'AND', rules: []};
  @Input() index: number;
  @Input() parentRuleSet: QueryRuleSet;

  defaultValue = EMPTY_QUERY_RULE_SET;

  templateNames = {
    buttonGroup: 'buttonGroup',
    switchGroup: 'switchGroup',
    removeButton: 'removeButton',
    entity: 'entity',
    field: 'field',
    operator: 'operator',
    input: 'input',
    empty: 'empty'
  };

  constructor(private changeDetectorRef: ChangeDetectorRef) {
    super();
  }

  private _config: QueryBuilderProps = {};

  @Input()
  get config() {
    return this._config;
  }

  set config(config) {
    CommonsUtils.defaults(this._config, config, DEFAULT_QUERY_BUILDER_PROPS);
    LogUtils.debug(this, 'æ¥è¯¢æé å¨éç½®', this._config);
  }

  @Input()
  get value(): QueryRuleSet {
    return this.ruleSet;
  }

  set value(value: QueryRuleSet) {
    this.ruleSet = value || {condition: 'AND', rules: []};
  }

  validate(control: AbstractControl): ValidationErrors | null {
    const errors: { [key: string]: any } = {};
    const ruleErrorStore = [];
    let hasErrors = false;

    if (!this.config.allowEmptyRuleSet && this.checkEmptyRuleInRuleSet(this.value)) {
      errors.empty = 'æ¥è¯¢å®ä¹ä¸è½ä¸ºç©º.';
      hasErrors = true;
    }

    this.validateRulesInRuleSet(this.value, ruleErrorStore);

    if (ruleErrorStore.length) {
      errors.rules = ruleErrorStore;
      hasErrors = true;
    }
    return hasErrors ? errors : null;
  }

  ruleSetStyle(index = 0) {
    const bgColor = Object.keys(THEME_COLORS).filter((color, i) => i === index);
    if (bgColor.length === 0) {
      bgColor[0] = 'blue';
    }
    return {
      'border-left-width': '5px',
      'border-left-style': 'solid',
      'border-left-color': THEME_COLORS[bgColor[0]]
    };
  }

  getOperators(field: DataField): DataItem[] {
    if (CommonsUtils.isFunction(this.config.operators)) {
      return (<Function>this.config.operators)(field);
    } else {
      const operators = [];
      Object.keys(this.config.operators).forEach(key => {
        if (key === field.key) {
          operators.push(this.config.operators[key]);
        }
      });
      return operators;
    }
  }

  getFields(entityKey?: string): DataField[] {
    if (this.config.entities && entityKey) {
      return this.config.fields.filter((field) => {
        return field && field['entityKey'] === entityKey;
      });
    } else {
      return this.config.fields;
    }
  }

  getField(rule: QueryRule): DataField {
    const fields = this.getFields(rule.entity);
    if (fields) {
      const matched = fields.filter(field => field.key === rule.field);
      if (matched && matched.length > 0) {
        return matched[0];
      }
    }
    return undefined;
  }

  getClassNames(...args): string {
    const styleClasses = this.config.styleClasses;
    const classNames = args.map((id) => styleClasses[id]).filter((c) => !!c);
    return classNames.length ? classNames.join(' ') : null;
  }

  isInValidRule(rule) {
    return this.config.allowRuleSet && rule.rules && rule.rules.length === 0;
  }

  getRuleClassName(rule): string {
    let cls = this.getClassNames('row', 'connector', 'transition');
    cls += ' ' + this.getClassNames(rule['condition'] ? 'ruleSet' : 'rule');
    // if (ruleMeta.invalid) {
    // cls += ' ' + this.getClassNames('invalidRuleSet');
    // }
    return cls;
  }

  getDefaultDataField(entity: DataEntity): DataField {
    if (!entity) {
      return null;
    } else if (entity.defaultField !== undefined) {
      return this.getDefaultValue(entity.defaultField);
    } else {
      const entityDataFields = this.config.fields.filter((field) => {
        return field && field['entityKey'] === entity.key;
      });
      if (entityDataFields && entityDataFields.length) {
        return entityDataFields[0];
      }
    }
  }

  getDefaultOperator(field: DataField): string {
    if (field && field['defaultOperator'] !== undefined) {
      return this.getDefaultValue(field['defaultOperator']);
    } else {
      const operators = this.getOperators(field);
      if (operators && operators.length) {
        //  return operators[0];
      }
    }
  }

  addRule(): void {
    const parent = this.ruleSet;
    if (this.config.addRule) {
      this.config.addRule(parent);
    } else {
      const field = this.config.fields[0];
      parent.rules = [
        ...parent.rules,
        {
          field: field.key,
          entity: field['entityKey'],
          operator: this.getDefaultOperator(field),
          value: this.getDefaultValue(field.defaultValue)
        }
      ];
    }
  }

  addRuleSet(): void {
    const parent = this.ruleSet;
    if (this.config.addRuleSet) {
      this.config.addRuleSet(parent);
    } else {
      parent.rules = [
        ...parent.rules,
        {condition: 'AND', rules: []}
      ];
    }
  }

  removeRule(rule: QueryRule): void {
    const parent = this.ruleSet;
    if (this.config.removeRule) {
      this.config.removeRule(rule, parent);
    } else {
      parent.rules = parent.rules.filter((r) => r !== rule);
    }
  }

  removeRuleSet(ruleSet?: QueryRuleSet): void {
    ruleSet = ruleSet || this.ruleSet;
    const parent = this.parentRuleSet;
    if (this.config.removeRuleSet) {
      this.config.removeRuleSet(ruleSet, parent);
    } else {
      parent.rules = parent.rules.filter((r) => r !== ruleSet);
    }
  }

  changeCondition(value): void {
    this.ruleSet.condition = value;
  }

  changeField(key: string, rule: QueryRule): void {

    const field: DataField = this.config.fields[key];

    if (field && field.defaultValue !== undefined) {
      rule.value = this.getDefaultValue(field.defaultValue);
    } else {
      delete rule.value;
    }

    rule.operator = this.getDefaultOperator(field);
  }

  changeEntity(entityValue: string, rule: QueryRule): void {
    if (this.disabled) {
      return;
    }

    // const currentEntity: DataEntity = this.config.entities.find((e) => e.value === entityValue);
    // const defaultDataField: DataField = this.getDefaultDataField(currentEntity);

    // if (defaultDataField) {
    //   this.changeDataField(defaultDataField.key, rule);
    // }
  }

  getDefaultValue(defaultValue: any): any {
    switch (typeof defaultValue) {
      case 'function':
        return defaultValue();
      default:
        return defaultValue;
    }
  }

  context(context?: {}) {
    return {
      qb: <QueryBuilderComponent>this,
      ...context
    };
  }

  private checkEmptyRuleInRuleSet(ruleSet: QueryRuleSet): boolean {
    if (!ruleSet || !ruleSet.rules || ruleSet.rules.length === 0) {
      return true;
    } else {
      return ruleSet.rules.some((item: QueryRuleSet) => {
        if (item.rules) {
          return this.checkEmptyRuleInRuleSet(item);
        } else {
          return false;
        }
      });
    }
  }

  private validateRulesInRuleSet(ruleSet: QueryRuleSet, errorStore: any[]) {
    if (ruleSet && ruleSet.rules && ruleSet.rules.length > 0) {
      ruleSet.rules.forEach((item) => {
        if ((item as QueryRuleSet).rules) {
          return this.validateRulesInRuleSet(item as QueryRuleSet, errorStore);
        } else if ((item as QueryRule).field) {
          const field = this.config.fields[(item as QueryRule).field];
          if (field && field['validator'] && field['validator'].apply) {
            const error = field['validator'](item as QueryRule, ruleSet);
            if (error != null) {
              errorStore.push(error);
            }
          }
        }
      });
    }
  }

}
