import {
  ChangeDetectionStrategy,
  Component,
  ComponentRef,
  ElementRef,
  EventEmitter,
  Input,
  Output,
  Renderer2,
  SimpleChanges,
  Type,
  ViewContainerRef
} from '@angular/core';
import {CommonsUtils} from '@er/utils';
import {ComponentUtils} from '../../utils/component';
import {DynaComponentUtils} from '../../utils/dyna';


@Component({
  selector: 'er-dyna-component',
  template: '',
  changeDetection: ChangeDetectionStrategy.OnPush
})
export class DynaCompComponent {
  @Input() type: Type<any> | string;
  @Input() props: {};
  @Output() onCreated = new EventEmitter();

  private component: ComponentRef<{}>;

  constructor(private viewContainerRef: ViewContainerRef,
              private el: ElementRef,
              private renderer: Renderer2) {
  }

  ngOnChanges(changes: SimpleChanges) {
    if (changes.type) {
      this.createComponent();
    }
  }

  ngOnDestroy() {
    if (this.component) {
      this.component.destroy();
      this.component = null;
    }
  }

  private createComponent() {
    if (this.type) {
      if (CommonsUtils.isString(this.type)) {
        this.component = DynaComponentUtils.createComponentByTemplate(
          this.viewContainerRef,
          <string>this.type
        );
      } else {
        this.component = DynaComponentUtils.createComponentByType(
          this.viewContainerRef,
          <Type<any>>this.type
        );
      }
      ComponentUtils.bind(this.component.instance, this.props);
      this.onCreated.emit(this.component);
      const parent = this.el.nativeElement.parentNode;
      this.renderer.removeChild(parent, this.el.nativeElement);
    }
  }
}
