import {Pipe, PipeTransform} from '@angular/core';

@Pipe({
  name: 'erKeyValue'
})
export class KeyValuePipe implements PipeTransform {
  transform(data: { [key: string]: any }): {}[] {
    const lst = [];
    Object.keys(data).map(key => {
      lst.push({key, value: data[key]});
    });
    return lst;
  }
}
