import {AbstractControl, AsyncValidatorFn} from '@angular/forms';
import {ApiDataProps} from '@er/types';
import {CommonsUtils} from '@er/utils';
import {map} from 'rxjs/operators';
import {ApiUtils} from '../../utils/api';

export const NgAsyncValidator = function (requestProps: ApiDataProps, errorKey?: string): AsyncValidatorFn {
  const fn = (control: AbstractControl) => {
    let props = requestProps;
    if (requestProps.query && CommonsUtils.isFunction(requestProps.query)) {
      const query = (requestProps.query as Function)(control.value);
      props = {
        ...props,
        query
      };
    }
    const httpRequest = ApiUtils.fetch(props)
      .pipe(
        map(data => {
          return !data ? null : {[errorKey]: true};
        })
      );
    return httpRequest;
  };
  return fn;
};


export const FormlyAsyncValidator = function (requestProps: ApiDataProps) {
  const fn = (control: AbstractControl) => {
    let props = requestProps;
    if (requestProps.query && CommonsUtils.isFunction(requestProps.query)) {
      const query = (requestProps.query as Function)(control.value);
      props = {
        ...props,
        query
      };
    }
    const httpRequest = ApiUtils.fetch(props)
      .pipe(map(data => !data)
      );
    return httpRequest.toPromise();
  };
  return fn;
};
