import {ChangeDetectionStrategy, Component, EventEmitter, Input, Output} from '@angular/core';
import {QueryRule} from '@er/types';
import {BaseComponent} from '../../../bases/component';
import {QueryBuilderComponent} from '../component';


@Component({
  selector: 'er-query-builder-rule',
  templateUrl: 'tpl.html',
  styleUrls: ['styles.scss'],
  changeDetection: ChangeDetectionStrategy.OnPush
})
export class QueryBuilderRuleComponent extends BaseComponent {

  @Input() ruleIndex: number;

  @Output() ruleChange = new EventEmitter();

  constructor(public qb: QueryBuilderComponent) {
    super();
  }

  private _rule: QueryRule;

  @Input()
  get rule(): QueryRule {
    return this._rule;
  }

  set rule(rule) {
    this._rule = rule;
    this.ruleChange.emit(this._rule);
  }
}
