import {
  Compiler,
  Component,
  ComponentFactory,
  ComponentFactoryResolver,
  ModuleWithComponentFactories,
  NgModule,
  Type,
  ViewContainerRef
} from '@angular/core';
import {ComponentRef} from '@angular/core/src/linker/component_factory';
import {CacheUtils} from '@er/utils';
import {DiUtils} from '../di';

const cacheKey = '$component-factory';

export class DynaComponentUtils {

  private static _componentFactoryResolver: ComponentFactoryResolver = null;

  static createComponentByType(hostViewContainerRef: ViewContainerRef,
                               componentType: Type<any>): ComponentRef<any> {
    if (!componentType) {
      return;
    }
    let componentFactory = CacheUtils.get(cacheKey, componentType);
    if (!componentFactory) {
      componentFactory = DynaComponentUtils.getComponentFactoryResolver().resolveComponentFactory(componentType);
      CacheUtils.put(cacheKey, componentType, componentFactory);
    }
    return DynaComponentUtils.createComponent(hostViewContainerRef, componentFactory);
  }

  static createComponentByTemplate(hostViewContainerRef: ViewContainerRef,
                                   template: string) {

    if (!template) {
      return;
    }
    let componentFactory: ComponentFactory<any> = CacheUtils.get<ComponentFactory<any>>(cacheKey, template);
    if (!componentFactory) {
      const component = DynaComponentUtils.createDecoratedComponent(null, template);
      const module = DynaComponentUtils.createNgModule(component);
      componentFactory = module.componentFactories.find(factory => factory.componentType === component);
      CacheUtils.put(cacheKey, template, componentFactory);
    }
    return DynaComponentUtils.createComponent(hostViewContainerRef, componentFactory);
  }

  private static getComponentFactoryResolver(): ComponentFactoryResolver {
    if (!DynaComponentUtils._componentFactoryResolver) {
      DynaComponentUtils._componentFactoryResolver = DiUtils.get<ComponentFactoryResolver>(ComponentFactoryResolver);
    }
    return DynaComponentUtils._componentFactoryResolver;
  }

  private static createDecoratedComponent(useClass: any, template: string) {
    const componentClass = useClass || class {
    };
    return Component({
      template: template
    })(componentClass);
  }

  private static createNgModule(component: Type<any>): ModuleWithComponentFactories<any> {
    const ngModule = NgModule({declarations: [component]})(class {
    });
    return DiUtils.get<Compiler>(Compiler).compileModuleAndAllComponentsSync(ngModule);
  }

  private static createComponent(hostViewContainerRef: ViewContainerRef,
                                 componentFactory: ComponentFactory<any>): ComponentRef<any> {
    hostViewContainerRef.clear();
    const componentRef = hostViewContainerRef.createComponent(componentFactory);
    return componentRef;
  }

}
