import {Location} from '@angular/common';
import {ActivatedRoute, NavigationExtras, Router} from '@angular/router';
import {StateNames} from '@er/types';
import {CommonsUtils, StatesUtils} from '@er/utils';
import {DiUtils} from '../di';

export class RouteUtils {

  static getActiveRoute() {
    let route = DiUtils.get<ActivatedRoute>(ActivatedRoute);
    while (route.firstChild) {
      route = route.firstChild;
    }
    return route;
  }

  static getActiveRouteData() {
    const route = RouteUtils.getActiveRoute();
    return route && route.snapshot.data;
  }

  static clearOutlet(outletName: string = 'primary', isRelative = false) {
    const extra = {};
    if (!isRelative) {
      extra['relativeTo'] = RouteUtils.getActiveRoute();
    }
    DiUtils.get<Router>(Router).navigate([{outlets: {[outletName]: null}}], extra);
  }

  static nav(commands: string | any[], paramsOrRelative?: {} | boolean, extras?: NavigationExtras) {
    if (CommonsUtils.isString(commands)) {
      commands = [commands];
    }
    if (!extras) {
      extras = <NavigationExtras>{};
    }
    if (paramsOrRelative && Object.keys(paramsOrRelative).length > 0) {
      CommonsUtils.set(<{}>extras, 'queryParams', paramsOrRelative);
    }
    if ((CommonsUtils.isString(commands) && !(<string>commands).startsWith('/'))
      || (paramsOrRelative === true)) {
      CommonsUtils.set(<{}>extras, 'relativeTo', RouteUtils.getActiveRoute());
    }
    DiUtils.get<Router>(Router).navigate(<any[]>commands, extras);
  }

  static back() {
    DiUtils.get<Location>(Location).back();
  }

  static forward() {
    DiUtils.get<Location>(Location).forward();
  }

  static refresh() {
    RouteUtils.navigate(StatesUtils.getValue(StateNames.nav)['url']);
  }

  static navigate(url: string): void {
    if (url.startsWith('http')) {
      window.location.href = url;
    } else {
      DiUtils.get<Router>(Router).navigateByUrl(url);
    }
  }
}
