import {ChangeDetectorRef, EventEmitter, Input, Output} from '@angular/core';
import {AbstractControl, FormControl, FormGroup, ValidationErrors} from '@angular/forms';
import {ApiRequestOptions, ButtonProps, DataEntity, DynaContent} from '@er/types';
import {CommonsUtils, DialogUtils} from '@er/utils';
import {Observable} from 'rxjs';
import {first} from 'rxjs/operators';
import {ApiUtils} from '../utils/api';
import {BaseComponent} from './component';

export abstract class BaseFormComponent extends BaseComponent {

  @Input() $id?: any;

  @Input() entity: DataEntity;

  @Input() apiEntry: string;

  @Input() options = {};

  @Input() submitRootKey: string;

  @Input() submitExcludeKeys: string | string[];

  @Input() resetKeys: string | string[];

  @Input() styleClass: any;

  @Input() style: any;

  @Input() formStyleClass: any;

  @Input() formStyle: any;

  @Input() buttonsStyleClass: any = 'text-center my-5';

  @Input() submitBtnStyleClass: any = 'btn btn-success';

  @Input() resetBtnStyleClass: any = 'btn btn-danger';

  @Input() initBtnStyleClass: any = 'btn btn-alert';

  @Input() header: DynaContent;

  @Input() footer: DynaContent;

  @Input() buttons?: ButtonProps[];

  @Input() getModelOptions: ApiRequestOptions;

  @Input() submitOptions: ApiRequestOptions;

  @Input() beforeSubmit: (context) => boolean | Observable<boolean>;

  @Input() afterSubmit: (context) => void | {} | Observable<any>;

  @Input() valueChanges: (context) => void;

  @Input() resetAfterSubmit: boolean;

  @Output() onSubmit: EventEmitter<{}> = new EventEmitter();

  @Output() onSubmitted: EventEmitter<{}> = new EventEmitter();

  @Output() onModel: EventEmitter<{}> = new EventEmitter();

  @Output() onFormValueChanges: EventEmitter<any> = new EventEmitter();

  form: FormGroup = new FormGroup({});

  templateNames = {
    header: 'header',
    buttons: 'buttons',
    footer: 'footer'
  };

  private initModel: {};

  runtimeData = {};

  private hasUpdated = false;

  constructor(protected cd: ChangeDetectorRef) {
    super();
  }

  private _model: {};

  @Input()
  get model() {
    return this._model;
  }

  set model(model) {
    this._model = CommonsUtils.omitBy(model, CommonsUtils.lodash().isUndefined);
    this.refresh();
  }

  private _modelId: any;

  ngOnInit() {
    super.ngOnInit();
    this.$subscriptions = this.form.valueChanges.subscribe(event => this.onFormValueChanges.emit(event));
  }

  @Input()
  get modelId() {
    return this._modelId;
  }

  set modelId(modelId) {
    if (modelId !== this.modelId) {
      this._modelId = modelId;
      if (!modelId || modelId === '') {
        this.resetModel();
      } else if (this.apiEntry) {
        ApiUtils.getById(this.apiEntry, modelId, this.getModelOptions)
          .subscribe(result => {
            this.model = result;
            this.form.updateValueAndValidity({onlySelf: false, emitEvent: true});
          });
      }
    }
  }

  get isFormValid() {
    if (!this.hasUpdated) {
      this.updateControls();
    }
    return this.form.valid;
  }

  get httpTag(): string {
    return `http${'-' + this.formId || ''}`;
  }

  get formId() {
    return this.$id;
  }

  get submitModel() {
    let model = {...this.model};
    if (this.submitRootKey) {
      model = this.model[this.submitRootKey];
    }
    if (this.submitExcludeKeys) {
      model = CommonsUtils.removeKeys(model, ...CommonsUtils.getArrayValue(this.submitExcludeKeys));
    }
    Object.keys(model).forEach(key => {
      if (key.startsWith('$_')) {
        delete model[key];
      }
    });
    return model;
  }

  formControl(fieldName): AbstractControl {
    return this.form.controls[fieldName];
  }

  fieldComponent(fieldName) {
    return this.form['components'][fieldName];
  }

  getFormErrorFields() {
    const errors = [];
    Object.keys(this.form.controls).forEach(key => {
      const controlErrors: ValidationErrors = this.form.get(key).errors;
      if (controlErrors != null) {
        Object.keys(controlErrors).forEach(keyError => {
          errors.push(key); // + ', err value: ', controlErrors[keyError]);
        });
      }
    });
    return errors;
  }

  patchModel(model) {
    this.model = Object.assign({}, this.model || {}, model);
  }

  patchRuntimeData(data) {
    Object.assign(this.runtimeData, data);
  }

  isFieldValid(path: string | string[]) {
    const field = this.field(path);
    if (field) {
      return field.valid;
    } else {
      return undefined;
    }
  }

  isFieldTouched(fieldNamePath: string) {
    const field = this.field(fieldNamePath);
    if (field) {
      return field.touched;
    } else {
      return undefined;
    }
  }

  updateControls(form?: FormGroup) {
    if (!form) {
      form = this.form;
    }
    Object.keys(form.controls).forEach(field => {
      const control = form.get(field);
      if (control instanceof FormControl) {
        control.markAsTouched({onlySelf: true});
        // control.updateValueAndValidity({onlySelf: true});
      } else if (control instanceof FormGroup) {
        this.updateControls(control);
      }
    });
    this.hasUpdated = true;
  }

  field(path: string | string[]): AbstractControl {
    return this.form.get(path);
  }

  resetForm() {
    this.form.reset({});
  }

  resetModel(reset?) {
    if (this.initModel) {
      this.model = CommonsUtils.copy(this.initModel);
    } else if (CommonsUtils.isJson(reset)) {
      this.model = {...reset};
    } else if (this.options['resetModel']) {
      this.options['resetModel']();
    } else {
      this.model = {};
    }
    this.form.reset(this.model, {emitEvent: true});
  }

  setInitModel() {
    this.initModel = CommonsUtils.copy(this.model);
    if (this.options['updateInitialValue']) {
      this.options['updateInitialValue']();
    }
  }

  refresh() {
    this.cd.detectChanges();
  }

  submit() {
    if (!this.isFormValid) {
      DialogUtils.error('æä½å¤±è´¥', 'è¯·å¡«åå®æ´ä¿¡æ¯åï¼åæ§è¡æ¬æä½ï¼');
      return;
    }
    let result;
    if (this.beforeSubmit) {
      result = this.beforeSubmit(this.getEventContext());
      if (result instanceof Observable) {
        (<Observable<boolean>>result)
          .pipe(first())
          .subscribe(data => {
            this.submitAfterBefore(data);
          });
      } else {
        this.submitAfterBefore(result);
      }
    } else {
      this.doSubmit();
    }
  }

  defaultSubmit(submitModel?) {
    this.$subscriptions = ApiUtils.save.call(this, this.apiEntry, submitModel || this.submitModel, {
      tag: this.formId,
      ...this.submitOptions || {}
    })
      .subscribe(data => {
        if (data.id && data.id !== this.model['id']) {
          this.model['id'] = data.id;
        }
        this.onSubmitted.emit(this.getEventContext({response: data, model: this.model, submitted: submitModel || this.submitModel}));
        if (this.afterSubmit) {
          const result = this.afterSubmit(this.getEventContext({submitModel: submitModel || this.submitModel, id: data.id}));
          if (result instanceof Observable) {
            (<Observable<any>>result)
              .pipe(first())
              .subscribe(dat => {
                this.doPostSubmit(dat);
              });
          } else {
            this.doPostSubmit(result);
          }
        } else {
          this.doPostSubmit();
        }
      });
  }

  getEventContext(other?) {
    return ({
      formlyForm: this,
      form: <FormGroup>this.form,
      model: this.model,
      modelId: this.modelId,
      entry: this.apiEntry,
      entity: <DataEntity>this.entity,
      ...other || {}
    });
  }


  private submitAfterBefore(result) {
    if (result !== false) {
      if (CommonsUtils.isJson(result) && !CommonsUtils.isEmpty(result)) {
        this.doSubmit(result);
      } else {
        this.doSubmit();
      }
    }
  }

  private doSubmit(submitModel?) {
    if (this.onSubmit.observers.length > 0) {
      this.onSubmit.emit(this.getEventContext({submitModel}));
    } else {
      this.defaultSubmit(submitModel);
    }
  }

  private doPostSubmit(model?) {
    if (model) {
      this.resetModel(model);
    } else if (this.resetKeys) {
      const keys = CommonsUtils.getArrayValue(this.resetKeys);
      keys.forEach(key => delete this.model[key]);
      delete this.model['id'];
      this.form.reset(this.model, {emitEvent: true});
    } else if (this.resetAfterSubmit !== false) {
      this.resetModel(this.entity && this.entity.initValue || {});
    }
  }
}
