import {Input} from '@angular/core';
import {Props} from '@er/types';
import {CommonsUtils, IdUtils} from '@er/utils';
import {BaseComponent} from './component';

export abstract class WrapperComponent<T extends Props = Props> extends BaseComponent {

  $props: T;

  @Input() outputContext: {}; // target component event context

  @Input() attrs: any;

  protected $defaultProps: T;

  _initProps() {
    this.$props = CommonsUtils.defaults({}, this.$props || {}, this.$defaultProps || {});
    if (!this.$props.$id) {
      this.$props.$id = IdUtils.getRandom();
    }
    if (!this.$props.$ext) {
      this.$props.$ext = {};
    }
    if (this.$props.$ext['apiDataProps']) {
      if (!this.$props.$ext['apiDataProps'].requestOptions) {
        this.$props.$ext['apiDataProps'].requestOptions = {};
      }
      CommonsUtils.defaults(this.$props.$ext['apiDataProps'].requestOptions, {
        context: this,
        tag: this.$props.$id
      });
    }
    this.onPropsInit();
  }

  _propsBind() {
    this.onPropsBind();
  }

  onPropsInit() {}

  onPropsBind() {
  }
}
