import {Pipe, PipeTransform} from '@angular/core';
import {CommonsUtils} from '@er/utils';

@Pipe({name: 'erPercent'})
export class PercentPipe implements PipeTransform {

  transform(num: number) {
    if (num && CommonsUtils.isNumber(num)) {
      return `${num * 100}%`;
    }
    return num;
  }
}
