import {LogService} from '../../services/log/service';
import {DiUtils} from '../di';


export class LogUtils {

  private static _logService: LogService = null;

  static debug(from, message, ...context: any[]) {
    LogUtils.getLogService().debug(from, message, context);
  }

  static info(from, message, ...context: any[]) {
    LogUtils.getLogService().info(from, message, context);
  }

  static warn(messages: any | any[]) {
    LogUtils.getLogService().warn(messages);
  }

  static error(from, message, ...context: any[]) {
    LogUtils.getLogService().error(from, message, context);
  }

  private static getLogService() {
    if (!LogUtils._logService) {
      LogUtils._logService = DiUtils.get<LogService>(LogService);
    }
    return LogUtils._logService;
  }
}
