import {KeyValueChanges} from '@angular/core';
import {CommonsUtils} from '@er/utils';

export class ChangesUtils {

  static getChangeByDiff(diff: KeyValueChanges<any, any>): {
    changed?: { key: string, value: any, from: any }[],
    added?: { key: string, value: any }[],
    deleted?: { key: string, value: any }[]
  } {
    if (!diff) { return {}; }
    const changes = {
      changed: [],
      added: [],
      deleted: []
    };
    if (diff) {
      diff.forEachChangedItem(r => {
        changes.changed.push({
          key: r.key,
          value: r.currentValue,
          from: r.previousValue
        });
      });
      diff.forEachAddedItem(r => {
        changes.added.push({
          key: r.key,
          value: r.currentValue
        });
      });
      diff.forEachRemovedItem(r => {
        changes.deleted.push({
          key: r.key,
          value: r.previousValue
        });
      });
    }
    return CommonsUtils.omitBy(changes, CommonsUtils.lodash().isEmpty);
  }
}
