import {ApiDataProps, ApiPayload, Order} from '@er/types';
import {CommonsUtils, DialogUtils, StatesUtils} from '@er/utils';
import * as esb from 'elastic-builder';
import {Query} from 'elastic-builder';
import {Observable, throwError} from 'rxjs';
import {map, tap} from 'rxjs/operators';
import {ApiUtils} from '../api';

export interface NextSeqProps {
  apiEntry?: string;
  parent?: string;
  query?: Query | Query[];
  parentKey?: string;
  seqKey?: string;
  seqLen?: number;
  withParent?: boolean;
}

export interface DistinctValueProps {
  apiProps?: ApiDataProps;
  id?: any;
}

export class DataUtils {

  static DEFAULT_ROOT_SEQ = '001';

  static DEFAULT_PROPS: NextSeqProps = {
    parentKey: 'parent.code',
    seqKey: 'code',
    seqLen: 3
  };

  static getNextValue(options: NextSeqProps): Observable<any> {
    const opt: NextSeqProps = CommonsUtils.defaults(options || {}, DataUtils.DEFAULT_PROPS);
    const q = CommonsUtils.getArrayValue(opt.query) || [];
    if (opt.parent) {
      q.push(esb.termQuery(opt.parentKey, opt.parent));
    }
    return ApiUtils.getByQuery(`${opt.apiEntry}`, <ApiPayload>{
      query: q,
      sort: {[opt.seqKey]: Order.DESC},
      size: 1
    }).pipe(
      map((data: any) => {
        const prefix = '0'.repeat(opt.seqLen);
        let next = 1;
        if (data.total !== 0) {
          const last = data.items[0];
          if (last[opt.seqKey]) {
            next = parseInt(last[opt.seqKey].replace(/^0+/, '')) + 1;
          } else {
            DialogUtils.error('éè¯¯', 'æ æ³çææ°çåºåå·');
            throwError('æ æ³çææ°çåºåå·');
          }
        }
        const seq = (prefix + next).slice(-prefix.length);
        if (opt.withParent && opt.parent) {
          return opt.parent + seq;
        } else {
          return seq;
        }
      })
    );
  }

  static checkDistinct(props: DistinctValueProps): Observable<boolean> {
    const result$ = ApiUtils.fetch({
      ...props.apiProps,
      size: 2
    }).pipe(
      map((data) => !data.items || data.items.length === 0 ||
        (data.items.length === 1 && data.items[0]['id'] === props.id)),
      tap(distinct => {
        if (!distinct) {
          DialogUtils.error('éªè¯å¤±è´¥', `æ°å¼ã${props.apiProps.queryValue}ãå·²ç»å­å¨ï¼è¯·ä¿®æ¹ååæ§è¡æ¬æä½ï¼`);
        }
      })
    );
    return result$;
  }

  static onNodeSelect(event, keyOrQuery) {
    const treeProps = event['$from'].$props;
    const tableId = CommonsUtils.get(treeProps, '$ext.$container.table.$id');
    if (tableId) {
      let q = keyOrQuery;
      const nodeCode = event.node.data && event.node.data.code;
      if (nodeCode && CommonsUtils.isString(keyOrQuery)) {
        q = esb.prefixQuery(keyOrQuery, nodeCode);
      }
      StatesUtils.update(tableId, <ApiPayload>{
        query: nodeCode ? q : esb.matchAllQuery()
      });
    }
  }
}
