import {Directive, ElementRef, Input, OnInit} from '@angular/core';
import {CommonsUtils, DomUtils} from '@er/utils';

@Directive({
  selector: '[erAnimate]'
})
export class AnimateDirective implements OnInit {

  @Input('erAnimate') animation: string;

  private _animations = [
    'fadeIn',
    'fadeInLeft',
    'fadeInRight',
    'fadeInUp',
    'fadeInDown',
    'zoomIn',
    'zoomInLeft',
    'zoomInRight',
    'zoomInUp',
    'slideInDown',
    'slideInLeft',
    'slideInRight',
    'slideInUp'
  ];

  constructor(private el: ElementRef) {
  }

  ngOnInit() {
    let classes = 'animated ';
    if (this.animation) {
      classes += this.animation;
    } else {
      classes += this._animations[CommonsUtils.getRandomNumber(this._animations.length - 1)];
    }
    DomUtils.addClasses(this.el.nativeElement, classes);
  }
}
