import {OnDestroy} from '@angular/core';
import {Subject, Subscription} from 'rxjs';
import {BaseClass} from './class';

/**
 * provide auto unsubscribe support
 */
export class Unsubscribe extends BaseClass implements OnDestroy {

  protected destroyed$: Subject<void> = new Subject<void>();

  protected _$subscriptions: Subscription[] = [];

  set $subscriptions(subscription) {
    this._$subscriptions.push(subscription);
  }

  ngOnDestroy(): void {

    this.stopSubscribe();

    this._$subscriptions
      .filter(sub => sub)
      .map(sub => sub.unsubscribe());

    for (const prop in this) {
      if (this[prop]) {
        const m = this[prop];
        if (m && m instanceof Subscription) {
          m.unsubscribe();
        }
      }
    }
  }

  stopSubscribe() {
    this.destroyed$.next();
    this.destroyed$.complete();
  }
}
