import {Inject, Injectable, InjectionToken, OnDestroy, Optional} from '@angular/core';
import {Title} from '@angular/platform-browser';
import {ScheduledTask, StateNames, Task} from '@er/types';
import {CommonsUtils, ConfigUtils, StatesUtils} from '@er/utils';
import {Subscription, timer} from 'rxjs';
import {distinctUntilChanged, filter, map} from 'rxjs/operators';
import {DiUtils} from '../../utils/di';

export const BACKGROUND_TASK_TOKEN = new InjectionToken<Task>('BACKGROUND_TASK_TOKEN');

/**
 * used to run background task globally
 */
@Injectable({
  providedIn: 'root'
})
export class TaskService implements OnDestroy {

  subs: Subscription[] = [];

  constructor(@Optional() @Inject(BACKGROUND_TASK_TOKEN) tasks: Task[] = []) {
    StatesUtils.create(StateNames.appTitle, ConfigUtils.getConfig().appTitle, true);
    setTimeout(() => {
      this.cleanStates();
      const _tasks = CommonsUtils.flatten(tasks);
      if (_tasks && _tasks.length > 0) {
        _tasks.forEach(task => this.startTask(<Task>task));
      }
    }, 2000);
  }

  updateTitle() {
    this.subs.push(
      StatesUtils.observe(StateNames.appTitle)
        .pipe(
          filter(state => state && state.hasOwnProperty('title')),
          map(state => state.title),
          distinctUntilChanged()
        )
        .subscribe(title => {
          DiUtils.get<Title>(Title).setTitle(title);
        })
    );
  }

  cleanStates() {
    this.subs.push(
      timer(30000, 30000)
        .subscribe(() => {
          let store = StatesUtils.getStore().value;
          Object.keys(store).forEach(key => {
            if (store[key]) {
              if (!store[key].preserved && (store[key].state && store[key].state.observers.length === 0)) {
                store = CommonsUtils.omit(store, key);
              }
            }
          });
          StatesUtils.getStore().next(store);
        })
    );
  }

  startTask(task: Task) {
    if (CommonsUtils.isFunction(task)) {
      (task as Function)();
    } else {
      const schedule = task as ScheduledTask;
      let context = schedule.context;
      if (CommonsUtils.isFunction(schedule.context)) {
        context = schedule.context();
      }
      if (schedule.interval) {
        this.subs.push(
          timer(0, schedule.interval)
            .subscribe(() => {
              schedule.run(context);
            })
        );
      } else {
        schedule.run(context);
      }
    }
  }

  ngOnDestroy(): void {
    this.subs.forEach(sub => {
      sub.unsubscribe();
    });
  }
}
