import {Directive, EventEmitter, Input, OnChanges, Output, SimpleChanges} from '@angular/core';
import {ApiDataProps} from '@er/types';
import {CommonsUtils} from '@er/utils';
import {BaseDirective} from '../../bases/directive';
import {ApiUtils} from '../../utils/api';
import {LogUtils} from '../../utils/log';

@Directive({
  selector: '[erApiData]'
})
export class ApiDataDirective extends BaseDirective implements OnChanges {

  @Input('erApiData') apiDataProps: ApiDataProps;

  @Output() onRequestData: Function;

  @Output() onDataReady = new EventEmitter();

  ngOnChanges(changes: SimpleChanges) {
    if (changes.apiDataProps
      && changes.apiDataProps.currentValue
      && (this.apiDataProps.autoFetch !== false || !changes.apiDataProps.isFirstChange())) {
      this.fetchData();
    }
  }

  fetchData() {
    if (CommonsUtils.isEmpty(this.apiDataProps)) {
      LogUtils.debug('apiDataDirective', 'æ²¡æå®ä¹apiDataProps');
      return;
    }
    ApiUtils.fetch(this.apiDataProps)
      .subscribe(data => {
        LogUtils.debug(this, 'apiDataå¾è®¢éæ°æ®', data);
        if (this.apiDataProps.data$) {
          this.apiDataProps.data$.next(data);
        }
        this.onDataReady.emit(data);
      });
  }
}
