import {Directive, Input, TemplateRef} from '@angular/core';

@Directive({
  selector: '[erTypedTemplate]'
})
export class TypedTemplateDirective {

  @Input('erTypedTemplate') type: string;

  constructor(public template: TemplateRef<any>) {}

  getType(): string {
    return this.type;
  }
}

