import {Title} from '@angular/platform-browser';
import {StateNames} from '@er/types';
import {StatesUtils} from '@er/utils';
import {distinctUntilChanged, filter, map} from 'rxjs/operators';
import {DiUtils} from '../utils/di';

export const updateTitleTask = function () {
  StatesUtils.observe(StateNames.appTitle)
    .pipe(
      filter(appTitle => appTitle && appTitle.hasOwnProperty('title')),
      map(appTitle => appTitle.title),
      distinctUntilChanged()
    )
    .subscribe(title => {
      DiUtils.get<Title>(Title).setTitle(title);
    });
};
