import {Pipe, PipeTransform} from '@angular/core';
import {DataItemProps} from '@er/types';
import {DataItemUtils} from '@er/utils';

@Pipe({name: 'erDataItems'})
export class DataItemsPipe implements PipeTransform {
  transform(items: {}[], config: DataItemProps | {}) {
    if (!items) {
      return items;
    }
    return DataItemUtils.transItems(items, config || {});
  }
}
