import {HttpEvent, HttpEventType, HttpHandler, HttpInterceptor, HttpRequest, HttpResponse} from '@angular/common/http';
import {Injectable} from '@angular/core';
import {StateNames} from '@er/types';
import {StatesUtils} from '@er/utils';
import {Observable, throwError} from 'rxjs';
import {catchError, tap} from 'rxjs/operators';

@Injectable()
export class HttpProgressInterceptor implements HttpInterceptor {

  private inProcess = 0;

  intercept(req: HttpRequest<any>, next: HttpHandler): Observable<HttpEvent<any>> {

    return next
      .handle(req).pipe(
        tap(event => {
          if (event.type === HttpEventType.Sent) {
            StatesUtils.update(StateNames.rootHttp, {inProcess: ++this.inProcess, request: req});
          } else if (event instanceof HttpResponse) {
            StatesUtils.update(StateNames.rootHttp, {inProcess: --this.inProcess, request: req});
          }
        }),
        catchError((err) => {
          StatesUtils.update(StateNames.rootHttp, {inProcess: --this.inProcess, request: req});
          return throwError(err);
        })
      );
  }
}
