import {Pipe, PipeTransform} from '@angular/core';
import {CommonsUtils} from '@er/utils';


@Pipe({
  name: 'erFilter'
})
export class FilterPipe implements PipeTransform {
  transform(arr: any[],
            criteria: { key: string, value: any } | any,
            matcher?: { startWith: boolean, contains: boolean, regex: boolean, pinyin: boolean }) {

    if (!criteria) return arr;

    return arr.filter(value => {
      if (CommonsUtils.isJson(value)) {
        return value[criteria.key] && value[criteria.key].toLowerCase().indexOf(criteria.value.toLowerCase()) > -1;
      } else
        return value === criteria;
    });
  }
}
