import {OnInit} from '@angular/core';
import {FormBuilder, Validators} from '@angular/forms';
import {DiUtils} from '../../utils/di/index';
import {AuthBaseComponent} from './auth.component';

export class AbstractLoginComponent extends AuthBaseComponent implements OnInit {

  ngOnInit() {
    this.form = DiUtils.get<FormBuilder>(FormBuilder).group({
      [this.userToken]: ['', Validators.required],
      [this.password]: ['', Validators.required],
      [this.rememberMe]: ['']
    });
  }
}
