import {ChangeDetectorRef, Pipe, PipeTransform} from '@angular/core';
import {CommonsUtils} from '@er/utils';
import {BehaviorSubject, Observable, timer} from 'rxjs';
import {mapTo, scan, startWith, switchMap, tap} from 'rxjs/operators';

@Pipe({
  name: 'erTracker'
})
export class TrackerPipe implements PipeTransform {

  currentNumber = 0;

  counter$ = new BehaviorSubject<number>(0);

  constructor(private cd: ChangeDetectorRef) {
    //   .subscribe();
  }

  transform(number: number, offset: number = 20): Observable<number> {
    this.currentNumber = number - CommonsUtils.getRandomNumber(offset);
    this.counter$.next(this.currentNumber);
    this.counter$
      .pipe(
        switchMap(endRange => {
          return timer(1000, 200)
            .pipe(
              mapTo(this.positiveOrNegative(endRange, this.currentNumber)),
              startWith(this.currentNumber),
              scan((acc, curr) => acc + curr),
              tap(n => console.log(n))
              //  takeWhile(this.takeUntil(endRange, this.currentNumber))
            );
        })
      ).subscribe(() => this.cd.detectChanges());
    return this.counter$;
  }

  private positiveOrNegative(endRange, currentNumber) {
    return endRange > currentNumber ? 1 : -1;
  }

  private takeUntil(endRange, currentNumber) {
    return endRange > currentNumber
      ? val => val <= endRange
      : val => val >= endRange;
  }

}
