import {Pipe, PipeTransform} from '@angular/core';
import {Style} from '@er/types';


@Pipe({name: 'erStyle'})
export class StylePipe implements PipeTransform {
  transform(src: string | Style) {
    if (typeof src === 'string') {
      return src;
    } else {
      return JSON.stringify(src);
    }
  }
}
