import {OnInit} from '@angular/core';
import {Router} from '@angular/router';
import {ConfigModel} from '@er/types';
import {ConfigUtils} from '@er/utils';
import {DiUtils} from '../../utils/di';
import {BaseComponent} from '../component';

export abstract class ModuleConfigComponent extends BaseComponent implements OnInit {

  ngOnInit(): void {
    const router = DiUtils.get<Router>(Router);
    ConfigUtils.configModule(this.moduleConfig() || {}, router.url);
  }

  abstract moduleConfig(): ConfigModel;
}
