import {Injectable} from '@angular/core';
import {ActivatedRouteSnapshot, Resolve, RouterStateSnapshot} from '@angular/router';
import {ErRouteData} from '@er/types';
import {CommonsUtils} from '@er/utils';
import {EMPTY, Observable, of} from 'rxjs';


@Injectable({
  providedIn: 'root'
})
export class PathPropsDataResolver implements Resolve<{}> {

  resolve(route: ActivatedRouteSnapshot, state: RouterStateSnapshot): Observable<{}> {
    const routeData: ErRouteData = route.data;
    if (routeData) {
      if (!CommonsUtils.isEmpty(routeData)) {
        const pathValues = route.url.map(seg => seg.path);
        if (routeData['$props']) {
          return of(CommonsUtils.get(routeData['$props'], pathValues.join('.')));
        }
      }
    }
    if (routeData.resource) {
      // return forkJoin([]).pipe(
      //   map(results => ({
      //     tree: results[0],
      //     table: results[1]
      //   }))
    }
    return EMPTY;
  }
}
