import {ApplicationRef, ViewContainerRef} from '@angular/core';
import {CacheUtils} from '@er/utils';

const cacheKey = '$rootInjectables';
let viewContainerRef: ViewContainerRef = null;

export class DiUtils {

  static injector = null;

  static get<T>(token): T {
    let cached = CacheUtils.get(cacheKey, token);
    if (!cached) {
      if (!DiUtils.injector) {
        throw new Error('rootInjector åå§åå¤±è´¥');
      }
      cached = DiUtils.injector.get(token);
      if (cached) {
        CacheUtils.put(cacheKey, token, cached);
      }
    }
    return cached;
  }

  static getRootViewContainerRef(): ViewContainerRef {
    if (!viewContainerRef) {
      const applicationRef = DiUtils.get<ApplicationRef>(ApplicationRef);
      const appInstance = applicationRef.components[0].instance;
      if (!appInstance.rootViewContainerRef) {
        const appName = applicationRef.componentTypes[0].name;
        throw new Error(`${appName} constructorä¸­æªå®ä¹rootViewContainerRef`);
      }
      viewContainerRef = appInstance.rootViewContainerRef;
    }
    return viewContainerRef;
  }

}
