import {Injectable} from '@angular/core';
import {LogLevel} from '@er/types';
import {CommonsUtils, ConfigUtils, DateUtils, DialogUtils} from '@er/utils';
import {HttpUtils} from '../../utils/http';

@Injectable({
  providedIn: 'root'
})
export class LogService {

  debug(from, message: string, ...context: any[]) {
    if (!ConfigUtils.getConfig().isProduction
      && ConfigUtils.getConfig().log.enableApp
      && ConfigUtils.getConfig().log.level <= LogLevel.DEBUG) {
      if ((typeof from) !== 'string' && from.constructor) {
        from = from.constructor.name;
      }
      if (ConfigUtils.getConfig().log.blackLList.indexOf(from) < 0) {
        this.getLogger().log(`[${DateUtils.getFormattedTime()}] %c DEBUG `,
          'color: white; background: green; font-size: 18px;',
          this.getLogData([from, message, ...context]));
      }
    }
  }

  info(from, message: string, ...context: any[]) {
    if (ConfigUtils.getConfig().log.level <= LogLevel.INFO) {
      if (typeof from !== 'string' && from.constructor) {
        from = from.constructor.name;
      }
      if (ConfigUtils.getConfig().log.blackLList.indexOf(from) < 0) {
        this.getLogger().log(`[${DateUtils.getFormattedTime()}] %c INFO `,
          'color: white; background: blue; font-size: 18px;',
          this.getLogData([from, message, ...context]));
      }
    }
  }

  warn(messages: any) {
    if (ConfigUtils.getConfig().log.level <= LogLevel.WARN) {
      this.getLogger().log(`[${DateUtils.getFormattedTime()}] %c WARN `,
        'color: white; background: blue; font-size: 18px;',
        this.getLogData(messages));
    }
  }

  error(from, message, ...context: any[]) {
    DialogUtils.error('éè¯¯', message);
    if (ConfigUtils.getConfig().log.level <= LogLevel.ERROR) {
      this.getLogger().log(`[${DateUtils.getFormattedTime()}] %c ERROR `,
        'color: white; background: red; font-size: 18px;',
        this.getLogData([from, message, ...context]));
      if (ConfigUtils.getConfig().log.httpPost) {
        if (CommonsUtils.isFunction(from)) {
          from = from.constructor.name;
        }
        this.getHttpLogger().log(DateUtils.getFormattedDateTime(),
          this.getLogData([from, message]));
      }
    }
  }

  protected getLogger(): Console {
    return console;
  }

  protected getHttpLogger(): Console {
    return <Console>{
      log: (message?: any, ...optionalParams: any[]) => {
        HttpUtils.request({
          ...ConfigUtils.getConfig().logHttpOptions,
          body: {
            message,
            ...optionalParams
          }
        });
      }
    };
  }

  private getLogData(messages: any) {
    return {
      ...messages
    };
  }
}
