import {HostBinding, Input, OnInit} from '@angular/core';
import {ControlValueAccessor} from '@angular/forms';
import {NO_OP} from '@er/types';
import {BehaviorSubject} from 'rxjs';
import {BaseComponent} from './component';


export class ControlComponent extends BaseComponent implements OnInit, ControlValueAccessor {

  @Input() parser: (value) => any;

  @Input() formatter: (model) => any;

  @Input() disabled = false;

  defaultValue: any;

  disabled$: BehaviorSubject<boolean> = new BehaviorSubject<boolean>(false);

  onTouched: () => void = NO_OP;
  onChange: (_: any) => void = NO_OP;

  protected innerValue: any;

  @HostBinding('style.opacity')
  get opacity() {
    return this.disabled ? 0.25 : 1;
  }

  @HostBinding('style.cursor')
  get cursor() {
    return this.disabled ? 'not-allowed' : 'default';
  }

  get value(): any {
    return this.innerValue;
  }

  set value(value: any) {
    if (!value || value !== this.innerValue) {
      const v = this.parser ? this.parser(value) : value;
      this.innerValue = v;
      this.onChange(v);
    }
  }

  ngOnInit() {
    if (this.defaultValue) {
      this.innerValue = this.defaultValue;
    }
  }

  updateTouch() {
    this.onTouched();
  }

  writeValue(model: any) {
    const v = this.formatter ? this.formatter(model) : model;
    if (v !== this.innerValue) {
      this.innerValue = v;
    }
  }

  registerOnChange(fn: any) {
    this.onChange = fn;
  }

  registerOnTouched(fn: any) {
    this.onTouched = fn;
  }

  setDisabledState(isDisabled: boolean): void {
    this.disabled = isDisabled;
    this.disabled$.next(isDisabled);
  }
}
