import {ComponentRef, Directive, ElementRef, Input, OnInit, Renderer2, ViewContainerRef} from '@angular/core';
import {Context} from '@er/types';
import {CommonsUtils} from '@er/utils';
import {BaseDirective} from '../../bases/directive';
import {DynaComponentUtils} from '../../utils/dyna';

@Directive({
  selector: '[erDynaTemplate]',
})
export class DynaTemplateDirective extends BaseDirective implements OnInit {

  @Input('erDynaTemplate') template: string;
  @Input() context: Context;

  private componentRef: ComponentRef<{}>;

  constructor(private viewContainerRef: ViewContainerRef,
              private el: ElementRef,
              private renderer: Renderer2) {
    super();
  }

  ngOnInit() {
    if (CommonsUtils.isEmptyString(this.template)) { this.template = this.el.nativeElement.innerHTML; }
    if (CommonsUtils.isEmptyString(this.template)) { return; }
    this.template = this.template.replace(/\[\[/g, '{{');
    this.template = this.template.replace(/]]/g, '}}');
    this.componentRef = DynaComponentUtils.createComponentByTemplate(
      this.viewContainerRef,
      this.template,
    );
    const parent = this.el.nativeElement.parentNode;
    this.renderer.removeChild(parent, this.el.nativeElement);
  }

  ngOnDestroy(): void {
    super.ngOnDestroy();
    if (this.componentRef) {
      this.componentRef.destroy();
      this.componentRef = null;
    }
  }
}
