import {StateNames} from '@er/types';
import {StatesUtils} from '@er/utils';
import {Observable} from 'rxjs';
import {distinctUntilChanged, filter, map} from 'rxjs/operators';


export const HTTP_STATE$: Observable<boolean> = StatesUtils.observe(StateNames.rootHttp)
  .pipe(
    distinctUntilChanged(),
    map(state => state && state.inProcess && state.inProcess > 0)
  );

export const HTTP_TAG_STATE$ = (httpTag): Observable<boolean> => {
  return StatesUtils.observe(StateNames.http, httpTag)
    .pipe(
      distinctUntilChanged(),
      filter(state => !!state),
      map(state => state && state.start)
    );
};

export const ROUTE_STATE$: Observable<boolean> = StatesUtils.observe(StateNames.nav)
  .pipe(
    distinctUntilChanged(),
    filter(state => !!state),
    map(state => state && state.start && state.outlet === 'primary')
  );

export const ROUTE_OUTLET_STATE$ = (outlet): Observable<boolean> => {
  return StatesUtils.observe(StateNames.nav)
    .pipe(
      distinctUntilChanged(),
      filter(state => !!state),
      map(state => state && state.start && state.outlet === outlet)
    );
};

export const WEB_SOCKET_STATES: Observable<string> = StatesUtils.observe(StateNames.socket)
  .pipe(
    distinctUntilChanged(),
    filter(state => !!state),
    map(state => state.state)
  );
