import {ChangeDetectorRef, NgZone, OnDestroy, Pipe, PipeTransform} from '@angular/core';
import {DateUtils} from '@er/utils';

@Pipe({
  name: 'erUpToNow',
  pure: false
})
export class UpToNowPipe implements PipeTransform, OnDestroy {

  private timer: number;

  constructor(private cd: ChangeDetectorRef, private ngZone: NgZone) {
  }

  transform(value: any, due?: Date) {
    const d = DateUtils.toDate(value);
    if (!d) {
      return '';
    }
    const span = DateUtils.getDateByNow(d, due);
    this.removeTimer();
    const timeToUpdate = (Number.isNaN(span.seconds)) ? 1000 : DateUtils.getUpdateSeconds(span.seconds) * 1000;
    this.timer = this.ngZone.runOutsideAngular(() => {
      if (typeof window !== 'undefined') {
        return window.setTimeout(() => {
          this.ngZone.run(() => this.cd.detectChanges());
        }, timeToUpdate);
      }
      return null;
    });
    return span.message;
  }

  ngOnDestroy(): void {
    this.removeTimer();
  }

  private removeTimer() {
    if (this.timer) {
      window.clearTimeout(this.timer);
      this.timer = null;
    }
  }


}

